import React from "react";
import useSWR from "swr";
import Card from 'react-bootstrap/Card';
import { Chip } from '@mui/material';
import { Icon } from '@iconify/react'
import CircularStatic from '../Components/loading'
import Style from '../styles/Prjbio.module.scss'
import Turtle from '../img/vlBqTaznmD4.jpg'
const fetcher = (url) => fetch(url).then((res) => res.json());

export default function GithubCardTest() {
  const { data, error } = useSWR(
    "https://api.github.com/users/culcat",
    fetcher
  );

  if (error) return "An error has occurred.";
  if (!data) return "Loading...";
  return (
    <div>
    

<Card className='bg-dark' style={{ width: 'auto' }}>
      <Card.Body>
   

        <Card.Text>
  <div className={Style.gitCard}>
     <a href="https://github.com/culcat"> <img className={Style.Gico} src={data.avatar_url} alt="" /></a>
<p >{data.login} </p><p>{data.name}</p><p>Подписчики: {data.followers} Репозитрии: {data.public_repos}</p></div>
<hr />
        <span> Я - Frontend веб-разработчик и UI/UX веб-дизайнер. Иногда занимаюсь графическим дизайном. Я живу в Ростовской области России (часовой пояс UTC+3 или MSK+0).
    Для разработки предпочитаю <Chip color="primary" variant="outlined" label='macOS' size='small' avatar=<Icon icon="carbon:mac-command" /> /> ,
     основной редактор кода <Chip color="primary"  variant="outlined"  label='VS Code' size='small' avatar=<Icon icon="mdi:vs-code"  />/>,  для проектирования веб-сайтов использую
     <Chip variant="outlined" color="primary"  label='Figma' size='small' avatar=<Icon icon="ph:figma-logo" color="white" />/>, для графического дизайна использую 
     <Chip variant="outlined" color="primary"  label='Photoshop' size='small' avatar=<Icon icon="simple-icons:adobephotoshop" color="white" />/>.</span>
     <br />
     <hr />
     <span> <p>А ещё мне нравится сериал <em><a href="https://www.kinopoisk.ru/series/252089/">«Доктор Кто».</a> </em></p></span>
        </Card.Text>

      </Card.Body>
    </Card>
   
 
    </div>
  );
}
